<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process238"></a>

<p><br></p>
<p><span class="rvts17">王者竞技场（</span><span class="rvts50">Arena of One</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">主线任务</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:570,Y:721</span><span class="rvts21">，地点：王者竞技场（</span><span class="rvts20">The Arena of One</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">获得竞技场的冠军</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1331.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">王者竞技场的比赛相当简单，只要队伍中有一名玩家首先到达宝座终点，便可以获得</span><span class="rvts20"></span><span class="rvts21">神</span><span class="rvts20"></span><span class="rvts21">的称号。接着便是一场无情的厮杀。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1332.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">最难通过的是有两处石头守卫的地方。它们会喷火和眩晕技能。尝试使用能大范围位移的飞行，跳跃或传送技能，可以非常容易的到达终点。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1333.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">到达终点王座部分，便会获得竞技场的第一名。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1334.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">然而此时，大锤达利斯赶到，她感觉这简直就是一场闹剧。于是破坏掉了整个竞技场，使整个局面变得混乱。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1335.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">当我们尝试逃出竞技场时，主角会遇到心中的</span><span class="rvts20"></span><span class="rvts21">神</span><span class="rvts20"></span><span class="rvts21">对竞技场称神失败事件的责备。尝试抵抗它，并选择</span><span class="rvts20"></span><span class="rvts21">挺直腰板拒绝下跪</span><span class="rvts20"></span><span class="rvts21">（如果选择屈服于他，他会瞬间杀掉主角，然而队伍的其他角色依然会进入与他的战斗状态）。</span>
<br><span class="rvts21">在这场战斗中，玩家队伍中有几个角色，就会出现几个神。因此，</span><span class="rvts20"></span><span class="rvts21">孤狼</span><span class="rvts20"></span><span class="rvts21">队伍应对此局面会比较容易。如果队伍中一共有</span><span class="rvts20">4</span><span class="rvts21">个人，那么就要打败</span><span class="rvts20">4</span><span class="rvts21">个神。但由于战斗中还会引起火流星的随机袭击，并最后形成岩浆表层（可使角色瞬间死亡）。</span>
<br><span class="rvts21">因此这场战斗必须尽可能快的结束，如果由于装备和等级原因需要持续很久才能打败它们，那么尽可能的使队伍中的一名角色先往马拉迪的信标处跑（此时还没有显示出信标位置）。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1336.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">打败心中的神后，最终会遇到大神</span><span class="rvts20"></span><span class="rvts21">泰坦</span><span class="rvts20"></span><span class="rvts21">，击败泰坦，可以从它身上获得很多史诗装备。（注意，打败泰坦并不算脱战，因此捡取物品也需要消耗</span><span class="rvts20">AP</span><span class="rvts21">点）</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1337.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">之后需要至少有一名角色迅速跑到马拉迪的信标处，在火流星形成的岩浆表面封锁道路前，成功传送所有队友到复仇女神号。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1338.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">与马拉迪谈话，开始接下来的事情。马拉迪建议我们去阿克斯，找达利斯算账。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1339.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在船上，会看到死去的队友和未加入我们队伍的其余源力者。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1340.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">如果此时队伍中有至少</span><span class="rvts20">1</span><span class="rvts21">名队友的话，我们可以尝试与他开辟一段私人之旅，共同渡过一个难忘的夜晚。注意，只能选择与一个队友过夜。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1341.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">再次找到复仇女神号的船头，与它互动，让它召唤出马拉迪，说你准备好了。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1342.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">此时马拉迪出现，与她对话后，便开始进入阿克斯的旅途。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



